  // const flare02 = new BABYLON.LensFlare(0.2, 1.0, new BABYLON.Color3(1, 1, 1), './textures/Flare3.png', lensFlareSystem)
  // const flare03 = new BABYLON.LensFlare(0.4, 0.4, new BABYLON.Color3(1, 0.5, 1), './textures/Flare.png', lensFlareSystem)
  // const flare04 = new BABYLON.LensFlare(0.1, 0.6, new BABYLON.Color3(1, 1, 1), './textures/Flare2.png', lensFlareSystem)
  // const flare05 = new BABYLON.LensFlare(0.3, 0.8, new BABYLON.Color3(1, 1, 1), './textures/Flare3.png', lensFlareSystem)

  // /*
  // * Keys:
  // * - 1: Day
  // * - 2: Evening
  // * - 3: Increase Luminance
  // * - 4: Decrease Luminance
  // * - 5: Increase Turbidity
  // * - 6: Decrease Turbidity
  //   * - 7: Move horizon to -50
  //   * - 8: Restore horizon to 0
  // */

  const setSkyConfig = (property, from, to) => {
    const keys = [
      { frame: 0, value: from },
      { frame: 100, value: to }
    ]

    const animation = new BABYLON.Animation('animation', property, frameRate, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CONSTANT)
    // const animation2 = new BABYLON.Animation('animationLight', property, frameRate, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CONSTANT)

    animation.setKeys(keys)
    // animation2.setKeys(keys)

    scene.stopAnimation(skybox)
    // scene.beginDirectAnimation(skybox, [animation, animation2], 0, 1000, false, 1) // 0.006 0.0001
    scene.beginDirectAnimation(skybox, [animation], 0, 1000, false, 1) // 0.006 0.0001
    // scene.beginDirectAnimation(skybox, [animation], 0, 2 * frameRate, true)
  }

  const setLightConfig = (property, target, from, to) => {
    const keys = [
      { frame: 0, value: from },
      { frame: 100, value: to }
    ]

    const animation = new BABYLON.Animation('animationLight', property, frameRate, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CONSTANT)
    animation.setKeys(keys)


    scene.stopAnimation(target)
    scene.beginDirectAnimation(target, [animation], 0, 1000, true, 0.006)
  }

  const setLightColorConfig = (property, target, from, to) => {
    const keys = [
      { frame: 0, value: from },
      { frame: 1000, value: to }
    ]

    const animation = new BABYLON.Animation('animationLightColor', property, frameRate, BABYLON.Animation.ANIMATIONTYPE_COLOR3, BABYLON.Animation.ANIMATIONLOOPMODE_CONSTANT)
    animation.setKeys(keys)

    scene.stopAnimation(target)
    scene.beginDirectAnimation(target, [animation], 0, 1000, true, 1)
  }

  // Set to Day
  // setSkyConfig('material.inclination', skyboxMaterial.inclination, -1)


  // // setLightConfig('intensity', scene.getMeshByID('Omni0'), 0.8, 0.2)

  // setLightConfig('intensity', sunLight, 1, 0.2)

  // var sunPos = [0.5, 1, 1.5, 2],
  //     i = -1;
  // (function f(){
  //     i = (i + 1) % sunPos.length;
  //     setSkyConfig('material.inclination', skyboxMaterial.inclination, sunPos[i])
  //     setTimeout(f, 1000);
  //  })();

  // // desde aquí
  // gun.get('world').get('environment').on(r => { // console.log(r)
  //   switch (r) {
  //     // case 82: BABYLON.ParticleHelper.CreateAsync('rain', scene, false).then(set => set.start()); break // 1
  //     case 49: setSkyConfig('material.inclination', skyboxMaterial.inclination, 0); break // 1
  //     case 50: setSkyConfig('material.inclination', skyboxMaterial.inclination, -0.5); break // 2
  //     case 51: setSkyConfig('material.inclination', skyboxMaterial.inclination, 0.5); break // 3
  //     case 52: setSkyConfig('material.luminance', skyboxMaterial.luminance, 0.1); break // 4
  //     case 53: setSkyConfig('material.luminance', skyboxMaterial.luminance, 1.0); break // 5
  //     case 54: setSkyConfig('material.turbidity', skyboxMaterial.turbidity, 40); break // 6
  //     case 55: setSkyConfig('material.turbidity', skyboxMaterial.turbidity, 5); break // 7
  //     case 56: setSkyConfig('material.cameraOffset.y', skyboxMaterial.cameraOffset.y, 50); break // 8
  //     case 57: setSkyConfig('material.cameraOffset.y', skyboxMaterial.cameraOffset.y, 0); break // 9
  //     default: break
  //   }
  // })

  // window.addEventListener('keydown', evt => gun.get('world').get('environment').put(evt.keyCode))

  gun.get('world').get('environment2').get('sun').get('position')
    .on(value => {
      scene.getMeshByName('skyBox').material.inclination = value || 0
    })

  // fog
  gun.get('world').get('environment2').get('fog').on(r => r === false ? scene.fogDensity = 0.0 : scene.fogDensity = 0.1)
