import * as BABYLON from 'babylonjs'
// import Hls from 'hls.js'
import gun from '../Gun'

const Streamer = scene => {
  const material = new BABYLON.StandardMaterial('mat', scene) // Create a material from the video
  material.backFaceCulling = false
  let videoTexture

  let mode

  // Attach the video material the a mesh
  const videoPlane = BABYLON.Mesh.CreatePlane('plane1', 4, scene)
  videoPlane.scaling.x = 1920 / 1080 // set aspect ratio
  videoPlane.position.z = 10
  videoPlane.position.y = 8
  videoPlane.material = material
  videoPlane.backFaceCulling = false

  const record = { recorder: null, recording: false }

  const playButton = document.querySelector('#play')
  const recordButton = document.querySelector('#record')
  const recordWebcam = document.querySelector('#rcam')

  if (navigator.mediaDevices) {
    console.log('getUserMedia supported.')
  }
  recordWebcam.addEventListener('click', () => {
    mode = "webcam"
    // BABYLON.VideoTexture.CreateFromWebCam(scene, stream => {
    //   material.diffuseTexture = stream
    //   videoPlane.material = videoMaterial
    // }, { maxWidth: 156, maxHeight: 156 })
    if (!record.ing) {
      return record.stream()
    }
    recordWebcam.innerText = 'Record webcam'
    if (record.ing.stop) { record.ing.stop() }
    record.ing = false
  }, false)

  recordButton.addEventListener('click', () => {
    mode = "screen"
    if (!record.ing) {
      return record.stream()
    }
    recordButton.innerText = 'Record'
    if (record.ing.stop) { record.ing.stop() }
    record.ing = false
  }, false)

  record.stream = async () => {
    let stream = null
    const displayMediaOptions = {
      audio: true,
      video: {
        width: 1280, height: 720
      }
    }
    const chunks = [] // we have a stream, we can record it

    try {
      if (mode === "webcam") {
        stream = await navigator.mediaDevices.getUserMedia(displayMediaOptions)
        recordWebcam.innerText = 'End'
        console.log(stream)
      } else {
        stream = await navigator.mediaDevices.getDisplayMedia(displayMediaOptions)
        recordButton.innerText = 'End'
      }

      record.ing = new MediaRecorder(stream)
      record.ing.ondataavailable = e => chunks.push(e.data)
      // record.ing.ondataavailable = e => console.log(e.data)
      record.ing.onstop = () => record.save(new Blob(chunks))
      record.ing.start()
    } catch (err) {
      console.error(`Error: ${err}`)
    }
  }

  record.save = data => {
    record.file = record.file || new FileReader()
    record.file.readAsDataURL(data)
    record.file.onloadend = () => {
      let b64 = record.file.result
      b64 = `data:video/webm${ b64.slice(b64.indexOf(';')) }`
      gun.get('test').get('screen').put(b64)
    }
  }

  playButton.addEventListener('click', () => {
    if (record.playing) {
      playButton.innerText = 'Play'
      videoTexture.video.pause()
      record.playing = false

      // navigator.mediaDevices.getUserMedia().getTracks().forEach(track => track.stop())
    } else {
      playButton.innerText = 'Stop'
      videoTexture.video.play()
      record.playing = true
    }
  }, false)

  gun.get('test').get('screen').on(data => {
    if (!data) { return }
    videoTexture = new BABYLON.VideoTexture('video', data, scene, false, false)
    material.diffuseTexture = videoTexture

    playButton.innerText = 'Stop'
    videoTexture.video.play()
    record.playing = true
  })


  // HLS

  // const video = document.getElementById('video')
  // if (Hls.isSupported()) {
  //   const hls = new Hls()
  //   hls.loadSource('https://video-dev.github.io/streams/x36xhzz/x36xhzz.m3u8')
  //   hls.attachMedia(video)
  //   hls.on(Hls.Events.MANIFEST_PARSED, () => {
  //     video.play()
  //   })
  // } else if (video.canPlayType('application/vnd.apple.mpegurl')) {
  //   video.src = 'https://video-dev.github.io/streams/x36xhzz/x36xhzz.m3u8'
  //   video.addEventListener('loadedmetadata', () => {
  //     video.play()
  //   })
  // }
}

export default Streamer
