import * as BABYLON from 'babylonjs'

const Stars = scene => { // Stars
  const particleSystem = new BABYLON.ParticleSystem('particles', 1000, scene)
  particleSystem.particleTexture = new BABYLON.Texture('assets/textures/Flare2.png', scene)

  // Where the particles come from
  particleSystem.emitter = BABYLON.Vector3.Zero() // the starting location

  // Colors of all particles
  particleSystem.color1 = new BABYLON.Color4(0.7, 0.8, 1.0, 1.0)
  particleSystem.color2 = new BABYLON.Color4(0.2, 0.5, 1.0, 1.0)
  particleSystem.colorDead = new BABYLON.Color4(0, 0, 0.2, 0.0)

  // Size of each particle (random between...
  particleSystem.minSize = 1 // 0.6
  particleSystem.maxSize = 8 // 1.8

  // Life time of each particle (random between...
  particleSystem.minLifeTime = 10 // 1000
  particleSystem.maxLifeTime = 1000 // 10000

  // Emission rate
  particleSystem.emitRate = 100
  particleSystem.preWarmStepOffset = 100
  particleSystem.preWarmCycles = 1000

  /** ***** Emission Space ******* */
  const sphereEmitter = particleSystem.createSphereEmitter(600)
  sphereEmitter.radiusRange = 0

  // Speed
  particleSystem.minEmitPower = 0
  particleSystem.maxEmitPower = 0
  particleSystem.updateSpeed = 0.005

  // Start the particle system
  particleSystem.start()
}

export default Stars
