import * as BABYLON from 'babylonjs'
import { WaterMaterial } from 'babylonjs-materials'
import gun from '../Gun'

const Water = scene => { // This creates a basic Babylon Scene object (non-mesh)
  const ground = scene.getMeshByName('land1')
  const skyBox = scene.getMeshByName('skyBox')
  const clouds = scene.getMeshByName('foutain')

  // scene.fogMode = BABYLON.Scene.FOGMODE_EXP
  // scene.fogDensity = 0.1
  // scene.fogStart = 20.0
  // scene.fogEnd = 60.0
  // scene.fogColor = new BABYLON.Color3(0.9, 0.9, 0.85)

  const water = BABYLON.Mesh.CreateGround('water', 2048, 2048, 16, scene, false)
  const waterMaterial = new WaterMaterial('waterMaterial', scene)
  waterMaterial.windForce = 0
  // waterMaterial.waveHeight = 0
  // waterMaterial.windDirection = new BABYLON.Vector2(1.0, 1.0)

  waterMaterial.waterColor = new BABYLON.Color3(0.1, 0.1, 0.6)
  waterMaterial.colorBlendFactor = 0.1
  waterMaterial.bumpHeight = 0.1
  waterMaterial.waveLength = 0.1

  waterMaterial.bumpTexture = new BABYLON.Texture('assets/textures/waterbump.png', scene)
  waterMaterial.refractionTexture.renderList.push(ground)
  waterMaterial.reflectionTexture.renderList.push(skyBox)
  waterMaterial.reflectionTexture.renderList.push(clouds)

  water.material = waterMaterial
  // water.position.y = 0.1
  // water end

  gun.get('world').get('environment2').get('ocean').get('altitude')
    .on(value => {
      water.position.y = value || 0
      console.log(value)
    })
}

export default Water
