import * as BABYLON from 'babylonjs'
import { CharacterController } from 'babylonjs-charactercontroller'
import PlayerLabel from './PlayerLabel'

function StreamPlayer (scene, engine, canvas, assetsManager, id, pos) { // Loading playable character
  // assetsManager.addMeshTask('character', '', 'assets/men_avatar/', 'scene.gltf').onSuccess = task => {
  // assetsManager.addMeshTask('character', '', 'assets/himeko/', 'scene.gltf').onSuccess = task => {
  // assetsManager.addMeshTask('character', '', 'assets/player/', 'Vincent.babylon').onSuccess = task => {
  assetsManager.addMeshTask('character', '', './scenes/', 'dummy3.babylon').onSuccess = task => {
    const player = task.loadedMeshes[0]
    const skeleton = task.loadedSkeletons[0]

    player.id = id
    player.name = 'player'
    player.checkCollisions = true
    // player.receiveShadows = true

    // const floorY = scene.getMeshByID('ground').getHeightAtCoordinates(pos[0], pos[2])
    // player.rotation = player.rotationQuaternion.toEulerAngles()
    // player.rotationQuaternion = null

    player.position = new BABYLON.Vector3(pos[0], pos[1], pos[2])
    player.rotation.y = pos[3]
    // player.scaling.scaleInPlace(0.01)

    skeleton.animationPropertiesOverride = new BABYLON.AnimationPropertiesOverride()
    skeleton.animationPropertiesOverride.enableBlending = true
    skeleton.animationPropertiesOverride.blendingSpeed = 0.05
    skeleton.animationPropertiesOverride.loopMode = 1
    skeleton.enableBlending(0.1)

    // skeleton.createAnimationRange('fall', 0, 16)
    skeleton.createAnimationRange('idle', 21, 65)
    // skeleton.createAnimationRange('jump', 70, 94)
    // skeleton.createAnimationRange('run', 100, 121)
    // skeleton.createAnimationRange('slideBack', 125, 129)
    // skeleton.createAnimationRange('strafeLeft', 135, 179)
    // skeleton.createAnimationRange('strafeRight', 185, 229)
    // skeleton.createAnimationRange('turnLeft', 240, 262)
    // skeleton.createAnimationRange('turnRight', 270, 292)
    // skeleton.createAnimationRange('walk', 300, 335)
    // skeleton.createAnimationRange('walkBack', 340, 366)

    const idle = skeleton.getAnimationRange('idle')
    // const walk = skeleton.getAnimationRange('walk')
    // const walk = skeleton.getAnimationRange('walk')
    // const walkBack = skeleton.getAnimationRange('walkBack')
    // const turnLeft = skeleton.getAnimationRange('turnLeft')
    // const turnRight = skeleton.getAnimationRange('turnRight')

    // console.log(skeleton)
    // IDLE
    // if (walk) scene.beginAnimation(skeleton, walk.from, walk.to, true)

    if (idle) scene.beginAnimation(skeleton, idle.from, idle.to, true)

    // if (walkRange) scene.beginAnimation(skeleton, walkRange.from, walkRange.to, true)

    PlayerLabel(player, id)
    // scene.beginAnimation(skeletons[0], 0, 120, 1.0, true);
    // return skeleton
  }
}

export default StreamPlayer
