import * as BABYLON from 'babylonjs'
import * as GUI from 'babylonjs-gui'
import { TerrainMaterial } from 'babylonjs-materials'
import gun from '../Gun'
// import { ipfsAdd, ipfsCat } from './Ipfs'

const b64heighmap3 = 'data:image/jpeg;base64,'

const b64heighmap2 = 'data:image/png;base64,'

const b64heighmap = 'data:image/png;base64,'

const b64mixTexture = 'data:image/png;base64,'

const Terraform = (scene, canvas, assetsManager) => {
  // let ground
  // Create terrain material
  const terrainMaterial = new TerrainMaterial('terrainMaterial', scene)
  terrainMaterial.specularColor = new BABYLON.Color3(0.5, 0.5, 0.5)
  terrainMaterial.specularPower = 64

  // Set the mix texture (represents the RGB values)
  const mixTexture = new BABYLON.DynamicTexture('mixTexture', 512, scene, true)
  const heightmap = new BABYLON.DynamicTexture('heightmap', 512, scene, true)

  // Painting parameters

  // let radius = config.radius || 12
  // let shadowBlur = config.shadowBlur || 80
  // let f = config.f || 0.02

  if (window.localStorage.getItem('config') === null) {
    window.localStorage.setItem('config', JSON.stringify({
      radius: 12,
      shadowBlur: 80,
      f: 0.02
    }))
  }

  const config = JSON.parse(window.localStorage.getItem('config'))

  function updateConfig () {
    window.localStorage.setItem('config', JSON.stringify(config))
  }

  // assetsManager.addImageTask('mixTexture', 'assets/terraform-textures/mixMap.png').onSuccess = task => {
  //   const context = mixTexture.getContext()
  //   const size = mixTexture.getSize()
  //   context.clearRect(0, 0, size.width, size.height)
  //   context.drawImage(task.image, 0, 0)
  //   mixTexture.update(true)
  //   // document.getElementById('canvasMixTexture').src = mixTexture._canvas.toDataURL()
  // }

  // assetsManager.addImageTask('heightmap', 'assets/textures2/heightMap.png').onSuccess = task => {
  //   const context = heightmap.getContext()
  //   const size = heightmap.getSize()
  //   context.clearRect(0, 0, size.width, size.height)
  //   context.drawImage(task.image, 0, 0)
  //   heightmap.update(true)
  //   // document.getElementById('canvasHeightMap').src = heightmap._canvas.toDataURL()
  // }

  let clearColor = '#0000ff'

  // const parent = heightmap
  let parent = mixTexture

  let context = parent._context
  const size = parent.getSize()

  if (clearColor) {
    context.fillStyle = clearColor
    context.fillRect(0, 0, size.width, size.height)
  }

  parent.update(true)

  let draw = false

  const onPointerDown = () => { draw = true }
  const onPointerUp = () => {
    draw = false
    context.save() // context.restore()
    // set canvasImg image src to dataURL
    // so it can be saved as an image
    // parent.update(true)
    // // // document.getElementById('canvasHeightMap').src = heightmap._canvas.toDataURL()
    // // // document.getElementById('canvasMixTexture').src = mixTexture._canvas.toDataURL()
    // heightmap.update(true)

    // // guardar png
    // const url = document.getElementById('canvasHeightMap').src.replace(/^data:image\/[^;]+/, 'data:application/octet-stream')
    // window.open(url)

    // guardar gun base64
    // gun.get('world').get('grid').get('land1').put({
    //   heightMap: heightmap._canvas.toDataURL(),
    //   mixTexture: mixTexture._canvas.toDataURL(),
    //   x: scene.getMeshByName('land1').position.x,
    //   y: scene.getMeshByName('land1').position.y,
    //   z: scene.getMeshByName('land1').position.z
    // })

    if (parent.name === 'mixTexture') {
      // const mt = await ipfsAdd(mixTexture._canvas.toDataURL())
      gun.get('world').get('grid').get('land1').get('mixTexture')
        .put(mixTexture._canvas.toDataURL())
    } else {
      // const hm = await ipfsAdd(heightmap._canvas.toDataURL())
      gun.get('world').get('grid').get('land1').get('heightMap')
        .put(heightmap._canvas.toDataURL())
    }
    updateConfig()
  }

  const onPointerMove = () => {
    if (draw) {
      const pickResult = scene.pick(scene.pointerX, scene.pointerY)
      const texcoords = pickResult.getTextureCoordinates()

      const centerX = texcoords.x * size.width
      const centerY = size.height - texcoords.y * size.height

      // draw circle
      context.beginPath()
      context.arc(centerX, centerY, config.radius || 12, 0, 2 * Math.PI, false)

      // context.filter = 'opacity(0.02)'
      context.filter = `opacity(${ config.f || 0.02 })`
      context.shadowBlur = config.shadowBlur || 80
      context.shadowOffsetX = 0
      context.shadowOffsetY = 0

      context.fill()
      context.lineWidth = 50

      if (parent.name === 'mixTexture') {
        console.log('mixTexture')
        // document.getElementById('canvasMixTexture').src = mixTexture._canvas.toDataURL()
      } else {
        console.log('heightmap')
        // document.getElementById('canvasHeightMap').src = heightmap._canvas.toDataURL()
        const buffer = context.getImageData(0, 0, 512, 512).data
        const vertexData = BABYLON.VertexData.CreateGroundFromHeightMap({
          width: 100,
          height: 100,
          subdivisions: 100,
          minHeight: 0,
          maxHeight: 14,
          buffer,
          bufferWidth: 512,
          bufferHeight: 512
        })
        // vertexData.applyToMesh(ground)
        vertexData.applyToMesh(scene.getMeshByName('land1'))
      }
      parent.update()
    }
  }

  canvas.addEventListener('pointerdown', onPointerDown, false)
  canvas.addEventListener('pointerup', onPointerUp, false)
  canvas.addEventListener('pointermove', onPointerMove, false)

  scene.onDispose = () => {
    canvas.removeEventListener('pointerdown', onPointerDown)
    canvas.removeEventListener('pointerup', onPointerUp)
    canvas.removeEventListener('pointermove', onPointerMove)
  }

  // terrainMaterial.mixTexture = mixTexture
  // Diffuse textures following the RGB values of the mix map
  // diffuseTexture1: Red
  // diffuseTexture2: Green
  // diffuseTexture3: Blue
  // diffuseTexture4: Alpha
  terrainMaterial.diffuseTexture1 = new BABYLON.Texture('assets/textures/floor.png', scene)
  terrainMaterial.diffuseTexture2 = new BABYLON.Texture('assets/textures/rock.png', scene)
  terrainMaterial.diffuseTexture3 = new BABYLON.Texture('assets/textures/grass.png', scene)
  terrainMaterial.diffuseTexture4 = new BABYLON.Texture('assets/textures/sand.jpg', scene)

  // Bump textures according to the previously set diffuse textures
  terrainMaterial.bumpTexture1 = new BABYLON.Texture('assets/textures/floor_bump.png', scene)
  terrainMaterial.bumpTexture2 = new BABYLON.Texture('assets/textures/rockn.png', scene)
  terrainMaterial.bumpTexture3 = new BABYLON.Texture('assets/textures/grassn.png', scene)
  terrainMaterial.bumpTexture4 = new BABYLON.Texture('assets/textures/sand.jpg', scene)

  // Rescale textures according to the terrain
  terrainMaterial.diffuseTexture1.uScale = terrainMaterial.diffuseTexture1.vScale = 10
  terrainMaterial.diffuseTexture2.uScale = terrainMaterial.diffuseTexture2.vScale = 10
  terrainMaterial.diffuseTexture3.uScale = terrainMaterial.diffuseTexture3.vScale = 10
  terrainMaterial.diffuseTexture4.uScale = terrainMaterial.diffuseTexture4.vScale = 10

  terrainMaterial.mixTexture = BABYLON.Texture.CreateFromBase64String(b64mixTexture, 'texture name', scene)

  // ipfsAdd(b64mixTexture)
  // ipfsAdd(b64heighmap3)

  // // eslint-disable-next-line no-undef
  // document.getElementById('canvasMixTexture').src = ipfsCa('QmQfCZ69BMrnvqHpJAGH1CABtkaCDqmUzJLrV62oE3AGsi') // b64mixTexture
  // document.getElementById('canvasHeightMap').src = ipfsCat('QmdcuHG9GjNupnbiPrQdKJjrzk4XumNMu44STuSYZYY1xi') // b64heighmap

  const cords = [
    { x: 0, z: 0, name: 'land1' },
    { x: 200, z: 200, name: 'land2' },
    { x: -200, z: -200, name: 'land3' },
    { x: -200, z: 200, name: 'land4' },
    { x: 200, z: -200, name: 'land5' },
    { x: 0, z: -200, name: 'land6' },
    { x: -200, z: 0, name: 'land7' },
    { x: 0, z: 200, name: 'land8' },
    { x: 200, z: 0, name: 'land9' }
  ]

  cords.forEach(land => {
    const ground = BABYLON.Mesh.CreateGroundFromHeightMap(land.name, b64heighmap3, 100, 100, 100, 0, 10, scene, false)
    ground.material = terrainMaterial
    ground.position = new BABYLON.Vector3(land.x, 0, land.z)
    ground.checkCollisions = true
    // gun.get('world').get('grid').get('region1').get('lands').put(land.name)
  })

  // gun.get('world').get('grid').get('region1').get('lands').map().on(land => {
  //   console.log(land)
  // })

  const plane = BABYLON.Mesh.CreatePlane('id1', 4, scene)
  plane.position = new BABYLON.Vector3(0, 8, 0)

  function cartel (Base64String) {
    const textureBase64 = new BABYLON.Texture('data:grass', scene, false, false, BABYLON.Texture.BILINEAR_SAMPLINGMODE, null, null, Base64String, true)
    // const textureBase64 = new BABYLON.Texture.CreateFromBase64String(Base64String, 'id', scene)
    const materialPlane = new BABYLON.StandardMaterial('texturePlane', scene)
    materialPlane.diffuseTexture = textureBase64
    materialPlane.backFaceCulling = false // Allways show the front and the back of an element
    plane.material = materialPlane
    // plane.material.subMaterials[0].diffuseTexture = textureBase64
  }

  gun.get('world').get('grid').get('land1').get('mixTexture').on(r => {
    console.log('mixTexture event')

    // const r = await ipfsCat(hash)

    terrainMaterial.mixTexture = BABYLON.Texture.CreateFromBase64String(r, 'texture name', scene)

    document.getElementById('canvasMixTexture').src = r
    cartel(r)
  })

  gun.get('world').get('grid').get('land1').get('heightMap').on(r => {
    console.log('heightMap event')

    // if (!scene.getMeshByID('land1')) {
    // ground = BABYLON.Mesh.CreateGroundFromHeightMap('land1', r, 100, 100, 100, 0, 10, scene, false)
    //   ground.material = terrainMaterial
    //   ground.position = new BABYLON.Vector3(0, 0, 0)
    //   ground.checkCollisions = true
    //   document.getElementById('canvasHeightMap').src = r
    // }
    document.getElementById('canvasHeightMap').src = r
    cartel(r)
  })

  // UI
  const toSize = isChecked => {
    if (isChecked) {
      context.fillStyle = '#000'
      context.shadowColor = '#000'
      clearColor = '#000'
    } else {
      context.fillStyle = '#ffffff'
      context.shadowColor = '#000'
    }
  }

  // const toPlace = isChecked => {
  //   if (isChecked) {
  //     parent = heightmap
  //     clearColor = '#000'
  //     context.fillStyle = '#ffffff'
  //     context.shadowColor = '#000'
  //   } else {
  //     clearColor = '#0000ff'
  //     parent = mixTexture
  //   }

  //   context = parent._context
  //   // size = parent.getSize()

  //   // if (clearColor) {
  //   //   context.fillStyle = clearColor
  //   //   context.fillRect(0, 0, size.width, size.height)
  //   // }
  //   // parent.update(true)
  // }

  const setColor = but => {
    switch (but) {
      case 0:
        context.fillStyle = '#0000ff'
        break
      case 1:
        context.fillStyle = '#00ff00'
        break
      case 2:
        context.fillStyle = '#ff0000'
        break
      case 3:
        context.fillStyle = '#ffffff'
        break
      default:
    }
    // context.shadowColor = '#0000ff'
  }

  const setMode = mode => {
    switch (mode) {
      case 0:
        clearColor = '#0000ff'
        parent = mixTexture
        break
      case 1:
        parent = heightmap
        clearColor = '#000'
        context.fillStyle = '#ffffff'
        context.shadowColor = '#000'
        break
      default:
        break
    }
  }

  const setSize = size => {
    switch (size) {
      case 0:
        context.fillStyle = '#ffffff'
        context.shadowColor = '#000'
        break
      case 1:
        context.fillStyle = '#000'
        context.shadowColor = '#000'
        clearColor = '#000'
        break
      default:
        break
    }
  }


  const displayValue = value => BABYLON.Tools.ToDegrees(value) || 0

  // var button = BABYLON.GUI.Button.CreateImageWithCenterTextButton("but", "Click Me", "textures/grass.png");
  const transformGroup = new GUI.CheckboxGroup('Transformation')
  transformGroup.addCheckbox('Big / Small', toSize)

  const colorGroup = new GUI.RadioGroup('Material')
  colorGroup.addRadio('Cesped', setColor, true)
  colorGroup.addRadio('Piedra', setColor)
  colorGroup.addRadio('Suelo', setColor)
  colorGroup.addRadio('Arena', setColor)

  const sizeGroup = new GUI.RadioGroup('Size')
  sizeGroup.addRadio('More', setSize, true)
  sizeGroup.addRadio('Less', setSize)

  const modeGroup = new GUI.RadioGroup('Terraform')
  modeGroup.addRadio('mixTexture', setMode, true)
  modeGroup.addRadio('heightmap', setMode)

  const planeForMainMenu = BABYLON.Mesh.CreatePlane('planeForMainMenu', 10)
  planeForMainMenu.position.y = 12
  // planeForMainMenu.lookAt(scene.activeCamera.position)
  const advancedTexture = GUI.AdvancedDynamicTexture.CreateForMesh(planeForMainMenu)

  // const advancedTexture = GUI.AdvancedDynamicTexture.CreateFullscreenUI('UI')
  const rotateGroup = new GUI.SliderGroup('Rotation', 'S')

  const force = value => { config.f = value }
  const area = value => { config.radius = value }
  const intensity = value => { config.shadowBlur = value }

  const sun = value => {
    // scene.getMeshByName('skyBox').material.inclination = value
    gun.get('world').get('environment2').get('sun').get('position').put(value)
  }
  const sea = value => {
    gun.get('world').get('environment2').get('ocean').get('altitude').put(value)
  }

  // sun.onPointerUpObservable.add(() => {
  //   console.log('up')
  // })

  rotateGroup.addSlider('force', force, 'unit', 0.02, 0.1, config.f, displayValue)
  rotateGroup.addSlider('area', area, 'unit', 8, 42, config.radius, displayValue)
  rotateGroup.addSlider('intensity', intensity, 'unit', 10, 100, config.shadowBlur, displayValue)
  rotateGroup.addSlider('sun', sun, 'unit', -0.60, 0.60, -0.35, displayValue)
  rotateGroup.addSlider('sea', sea, 'unit', -1, 8, 0, displayValue)
  // rotateGroup.sun.onValueChangedObservable.add(value => {
  //   console.log(value)
  //   // header.text = "Y-rotation: " + (BABYLON.Tools.ToDegrees(value) | 0) + " deg";
  //   // if (skull) {
  //   //     skull.rotation.y = value;
  //   // }
  // })

  const selectBox = new GUI.SelectionPanel('sp', [transformGroup, sizeGroup, modeGroup, colorGroup, rotateGroup])
  selectBox.width = 0.20
  selectBox.height = 0.80
  selectBox.horizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_RIGHT
  selectBox.verticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_BOTTOM

  selectBox.color = '#ffff9940'
  selectBox.background = '#00000040'
  selectBox.barColor = '#684502'
  selectBox.headerColor = 'cyan'
  selectBox.buttonColor = 'orange'
  selectBox.buttonBackground = '#684502'

  advancedTexture.addControl(selectBox)

  // left
  const selectBox2 = new GUI.SelectionPanel('sp')
  selectBox2.width = 0.25
  selectBox2.height = 0.48
  selectBox2.paddingLeft = 60
  selectBox2.horizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_LEFT
  selectBox2.verticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_BOTTOM

  advancedTexture.addControl(selectBox2)


  const transformGroup2 = new GUI.CheckboxGroup('Environment')

  transformGroup2.addCheckbox('Streaming', isChecked => {
    if (isChecked) {
      gun.get('world').get('cine').put(true)
      // BABYLON.ParticleHelper.CreateAsync('rain', scene, true).then(set => set.start())
    } else {
      gun.get('world').get('cine').put(false)
      // BABYLON.ParticleHelper.CreateAsync('rain', scene, true).then(set => set.stop())
    }
  })

  transformGroup2.addCheckbox('Fog', isChecked => gun.get('world').get('environment2').get('fog').put(isChecked))

  selectBox2.addGroup(transformGroup2)

  // let alpha = 0
  // scene.registerBeforeRender(() => {
  //   scene.fogDensity = Math.cos(alpha) / 10
  //   alpha += 0.02
  // })

  // // GUI
  // const panel = new GUI.StackPanel()
  // advancedTexture.addControl(panel)
  // const advancedTexture2 = GUI.AdvancedDynamicTexture.CreateFullscreenUI('UI')

  // const button = GUI.Button.CreateImageOnlyButton('but', 'textures/grass.png')
  // button.width = 0.2
  // button.height = '40px'
  // button.color = 'white'
  // button.background = 'green'

  // const textblock = new GUI.TextBlock('textblock', '')
  // textblock.width = 0.2
  // textblock.height = '40px'
  // textblock.color = 'white'
  // panel.addControl(textblock)

  // button.onPointerUpObservable.add(() => {
  //   textblock.text = 'Up!'
  // })
  // button.onPointerEnterObservable.add(() => {
  //   textblock.text = 'Enter!'
  // })
  // button.onPointerOutObservable.add(() => {
  //   textblock.text = 'Out!'
  // })
  // button.onPointerMoveObservable.add(coordinates => {
  //   const relative = button.getLocalCoordinates(coordinates)
  //   textblock.text = `${ relative.x }, ${ relative.y }`
  // })
  // advancedTexture2.addControl(button)
}

export default Terraform
