import * as BABYLON from 'babylonjs'

function MyLoadingScreen (/* variables needed, for example: */ text) { // init the loader
  this.loadingUIText = text
}

MyLoadingScreen.prototype.displayLoadingUI = function () {
  console.log(this.loadingUIText)
}
MyLoadingScreen.prototype.hideLoadingUI = () => {
  console.log('Loaded!')
}


// const loading = new Audio('audio/loading.mp3', {
//   loop: true,
//   autoplay: true
// })


// There are other ways also to show the loading indicator −
// BABYLON.SceneLoader.ShowLoadingScreen = true; //false to disable it
// To manually hide and show the loading screen, execute the following.
// engine.displayLoadingUI();
// engine.hideLoadingUI();

const Am = (scene, engine) => { // Invisible borders
  // assetsManager
  // const loading = new BABYLON.Sound('Ambient', 'audio/loading.mp3', scene, null, {
  //   loop: true,
  //   autoplay: true
  // })
  const assetsManager = new BABYLON.AssetsManager(scene)
  assetsManager.useDefaultLoadingScreen = false
  scene.ShowLoadingScreen = false

  // const assetsManager = new BABYLON.AssetsManager(scene)
  // But you can also do it on the assets manager itself (onTaskSuccess, onTaskError)
  assetsManager.onTaskError = task => {
    console.log(`error while loading ${ task.name }`)
  }
  assetsManager.onTaskSuccess = task => {
    // console.log(`TaskSuccess ${ task.name }`)
    // shadowGenerator.getShadowMap().renderList.push(task.loadedMeshes[0])
  }

  // assetsManager.onTaskErrorObservable.add(task => console.log('task failed', task.errorObject.message, task.errorObject.exception))
  assetsManager.onProgress = (remainingCount, totalCount, lastFinishedTask) => {
    // console.log(lastFinishedTask)
    engine.displayLoadingUI()
    engine.loadingUIText = `We are loading the scene. ${ remainingCount } out of ${ totalCount } items still need to be loaded.`
  }
  assetsManager.onFinish = tasks => {
    // loading.stop()
    // engine.hideLoadingUI()
    engine.loadingUIText = 'Finish'
    engine.runRenderLoop(() => scene.render())
  }
  engine.loadingUIBackgroundColor = 'Black'
  engine.loadingScreen = new MyLoadingScreen("I'm loading :-) !!")
  // loading.play()

  return assetsManager
}

export default Am
